@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
:root {
  --primary-color: #da042a;
  --primary-color-dark: #c00427;
  --secondary-color: #15151f;
  --secondary-color-dark: #0a0b0f;
  --text-light: #6b7208;
  --extra-light: #f8f7fd;
  --white: #ffffff;
  --max-width: 100%;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.section__container {
  max-width: var(--max-width);
  margin: auto;
  padding: 5rem 1rem;
}
.section__subheader {
  font-size: 1rem;
  font-weight: 500;
  color: var(--primary-color);
}
.section__header {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 3.5rem;
  color: var(--secondary-color-dark);
}
.section__description {
  margin-bottom: 2rem;
  color: var(--text-light);
}
.btn {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  color: var(--white);
  background-color: var(--primary-color);
  outline: none;
  border: none;
  border-radius: 5px;
  transition: 0.3s;
  cursor: pointer;
}
.btn:hover {
  background-color: var(--primary-color-dark);
}
img {
  width: 100%;
  height: auto;
  display: flex;
}

.logo img {
  width: 200px;
  height: 70px;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
html,
body {
  scroll-behavior: smooth;
  cursor: url("assets/client-1.jpg"), auto;
}

body {
  .poppins-regular {
    font-family: "Poppins", serif;
    font-weight: 400;
    font-style: normal;
  }

  .poppins-medium {
    font-family: "Poppins", serif;
    font-weight: 500;
    font-style: normal;
  }

  .poppins-semibold {
    font-family: "Poppins", serif;
    font-weight: 600;
    font-style: normal;
  }
}
.header {
  padding-block: 5rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("assets/Logo.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
nav {
  position: fixed;
  isolation: isolate;
  top: 0;
  width: 100%;
  margin: auto;
  max-width: var(--max-width);
  z-index: 9;
}
.nav__bar {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  background-color: var(--secondary-color-dark);
}
.nav__menu__btn {
  font-size: 1.5rem;
  color: var(--white);
  cursor: pointer;
}
.nav__links {
  position: absolute;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--secondary-color);
  gap: 2rem;
  transform: translateY(-100%);
  z-index: -1;
}
.nav__links.open {
  transform: translateY(0);
}

.nav__links a {
  color: var(--white);
  transition: 0.3s;
}

.nav__links a:hover {
  color: var(--primary-color);
}
.header__content {
  max-width: 600px;
}
.header__content h1 {
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: 600;
  line-height: 4rem;
  color: var(--white);
}
.banner__container {
  display: grid;
  grid-auto-rows: minmax(0, 350px);
}
.banner__card {
  padding: 5rem 2rem;
  display: grid;
  place-content: center;
}
.banner__card:nth-child(1) {
  background-color: var(--secondary-color);
}
.banner__card:nth-child(2) {
  background-color: var(--secondary-color-dark);
}
.banner__card h4 {
  max-width: 300px;
  margin: auto;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--white);
}
.banner__image img {
  height: 100%;
  object-fit: cover;
}
.experience__container {
  display: grid;
  gap: 2rem;
}
.experience__image img {
  max-width: 500px;
  margin: auto;
}
.service {
  background-color: var(--extra-light);
}

.service__container {
  text-align: center;
}
.service__grid {
  margin-top: 4rem;
  display: grid;
  gap: 4rem 2rem;
}
.service__card img {
  max-width: 150px;
  margin-inline: auto;
  margin-bottom: 1rem;
  border-radius: 100%;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
}
.service__card h4 {
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--secondary-color-dark);
}
.service__card p {
  color: var(--text-light);
}
.customisation {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("assets/customisation.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: repeat;
}
.customisation__container {
  text-align: center;
}
.customisation__container :is(.section__header, .section__description) {
  max-width: 750px;
  margin: auto;
  color: var(--white);
}
.customisation__grid {
  margin-top: 4rem;
  display: grid;
  gap: 4rem 2rem;
}
.customisation__card h4 {
  font-size: 3rem;
  font-weight: 400;
  color: var(--white);
}
.customisation__card p {
  color: var(--white);
}
.price__container {
  text-align: center;
}
.price__grid {
  margin-top: 4rem;
  display: grid;
  gap: 2rem 1rem;
}
.price__card {
  position: relative;
  overflow: hidden;
  padding: 4rem 1rem;
  border-top: 5px solid var(--extra-light);
  border-radius: 10px;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}
.price__card:hover {
  border-color: var(--primary-color);
  background-color: var(--extra-light);
}
.price__card__ribbon {
  position: absolute;
  width: fit-content;
  top: 2rem;
  right: -4.5rem;
  transform: rotate(45deg);
  padding: 5px 5rem;
  font-size: 0.9rem;
  color: var(--white);
  background-color: var(--primary-color);
  transition: 0.3s;
}
.price__card:hover .price__card__ribbon {
  font-size: 1rem;
}
.price__card h4 {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--secondary-color-dark);
}
.price__card h3 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
  font-weight: 600;
  color: var(--secondary-color-dark);
  transition: 0.3s;
}
.price__card:hover h3 {
  color: var(--primary-color);
}
.price__card p {
  margin-bottom: 1rem;
  color: var(--text-light);
}
.price__card .btn {
  margin-top: 1rem;
  min-width: 150px;
}
.contact {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("assets/contact.jpg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}
.contact__container :is(.section__header, .section__description) {
  color: var(--white);
}
.contact__btns {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.contact__btns .btn:nth-child(1) {
  background-color: transparent;
  border: 2px solid var(--white);
}
.contact__btns .btn:nth-child(2) {
  color: var(--primary-color);
  background-color: var(--white);
  border: 2px solid var(--white);
}
.testimonial__container {
  text-align: center;
}
.swiper {
  width: 100%;
  margin-top: 4rem;
  padding-bottom: 4rem;
}
.testimonial__card {
  max-width: 600px;
  margin: auto;
}
.testimonial__card img {
  max-width: 200px;
  margin: auto;
  margin-bottom: 1rem;
  border-radius: 100%;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
}
.testimonial__card p {
  margin-bottom: 1rem;
  color: var(--secondary-color-dark);
}
.testimonial__card h4 {
  font-size: 1.2rem;
  font-weight: 500;
  color: var(--primary-color);
}
.footer {
  background-color: var(--secondary-color-dark);
}
.subscribe__container {
  padding-bottom: 0;
  display: grid;
  gap: 2rem;
}
.subscribe__content .section__header {
  color: var(--white);
}
.subscribe__form form {
  width: 100px;
  max-width: 400px;
  margin: auto;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.subscribe__form input {
  width: 100%;
  padding: 0.75rem 1rem;
  outline: none;
  border: none;
  border-radius: 5px;
}
.footer__container {
  display: grid;
  gap: 4rem 2rem;
}
.footer__logo {
  margin-bottom: 2rem;
}
.footer__socials {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.footer__socials a {
  padding: 5px 10px;
  font-size: 1.25rem;
  color: var(--text-light);
  background-color: var(--secondary-color);
  border-radius: 100%;
  transition: 0.3s;
}
.footer__socials a:hover {
  color: blue;
  background-color: var(--white);
}
.footer__col h4 {
  margin-bottom: 2rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--white);
}
.footer__links li {
  margin-bottom: 1rem;
}
.footer__links a {
  color: var(--text-light);
  transition: 0.3s;
}
.footer__links a:hover {
  color: var(--white);
}
.footer__links p {
  color: var(--text-light);
}
.footer__links span {
  font-weight: 600;
  color: var(--white);
}
.footer__bar {
  padding: 1rem;
  font-size: 0.8rem;
  color: var(--white);
  background-color: var(--secondary-color);
  text-align: center;
}

@media (width>480px) {
  .header__content h1 {
    font-size: 4rem;
    line-height: 5rem;
  }
  .banner__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .banner__image {
    grid-column: 1/3;
  }
  .service__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .customisation__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .price__grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__col:first-child {
    grid-column: 1/3;
  }
}
@media (width>768px) {
  nav {
    padding: 2rem 1rem;
    position: static;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .nav__bar {
    padding: 0;
    background-color: transparent;
  }
  .nav__menu__btn {
    display: none;
  }
  .nav__links {
    padding: 0;
    width: unset;
    position: static;
    transform: none;
    flex-direction: row;
    background-color: transparent;
    align-items: center;
    justify-content: end;
  }
  .header {
    padding-top: 0;
  }

  .header__content h1 {
    font-size: 5rem;
    line-height: 6rem;
  }
  .banner__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .banner__image {
    grid-column: 3/5;
  }
  .experience__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .service__grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .customisation__grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .price__grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .contact__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .contact__content {
    grid-column: 2/3;
  }
  .subscribe__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .footer__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .footer__col:first-child {
    max-width: 400px;
  }
}
@media (width>1024px) {
  .price__grid {
    gap: 2rem;
  }
}
